window.dom = {
	create(elementString) {
		const template = document.createElement('template')
		template.innerHTML = elementString
		// Êàë‰ª¨Â∞ÜÊñ∞ËäÇÁÇπÂàõÂª∫Âú®template‰∏ãÈù¢
		// ÁÑ∂ÂêéÂ∞ÜÂàõÂª∫Âú®template‰∏ãÈù¢ÁöÑËäÇÁÇπappendÂà∞Ë¶ÅË¢´Ê∑ªÂä†ÁöÑËäÇÁÇπÂêéÈù¢
		// appendÁöÑÁâπÊÄßÊòØÁßªÈô§ÂéüËäÇÁÇπ‰∏ãÁöÑÂÜÖÂÆπÔºåÂπ∂Â∞ÜËøô‰∫õËäÇÁÇπÂÜÖÂÆπÊîæÂà∞Êñ∞ËäÇÁÇπ‰∏ã„ÄÇ
		// ËÄåÊàë‰ª¨ÂàõÂª∫Âú®template‰∏ãÁöÑËäÇÁÇπÊòØÂú®ÂÖ∂content.firstChildÂÜÖ„ÄÇ
		return template.content.firstChild
	},
	// Ê∑ªÂä†‰∏∫Â≠êËäÇÁÇπ
	append(parentNode, node) {
		parentNode.appendChild(node)
	},
	// Ê∑ªÂä†Âà∞ËäÇÁÇπÁöÑÂâçÈù¢
	before(sibling, node) {
		sibling.parentNode.insertBefore(node, sibling)
	},
	// Ê∑ªÂä†Âà∞ËäÇÁÇπÁöÑÂêéÈù¢
	after(sibling, node) {
		sibling.parentNode.insertBefore(node, sibling.nextSibling)
	},
	// Â∞Ü‰∏Ä‰∏™ËäÇÁÇπÂåÖË£π‰ΩèÂè¶‰∏Ä‰∏™ËäÇÁÇπÔºåÂç≥Áªô‰∏Ä‰∏™ËäÇÁÇπÂàõÂª∫‰∏Ä‰∏™Áà∂‰∫≤
	wrap(wrapNode, wrappedNode) {
		this.before(wrappedNode, wrapNode)
		this.append(wrapNode, wrappedNode)
	},

	// Âà†Èô§Êìç‰Ωú
	remove(node) {
		return node.parentNode.removeChild(node)
	},
	// Ê∏ÖÁ©∫‰∏Ä‰∏™ËäÇÁÇπ‰∏ãÁöÑÊâÄÊúâËäÇÁÇπ
	empty(node) {
		let child = node.firstChild
		let result = []
		while (child) {
			// Áî±‰∫é = Êìç‰ΩúÁ¨¶ÊòØ‰∏Ä‰∏™ÊµÖÊã∑Ë¥ùÔºåÊâÄ‰ª•ËøôÈáå‰∏çËÉΩ‰ΩøÁî®childÊù•Âà†Èô§ÔºåÂè™ËÉΩÁî®‰ΩúÂà§Êñ≠Ôºå
			// Âõ†‰∏∫Êâæ‰∏çÂà∞parentÔºåÂè™ËÉΩÁî®node.firsChild
			result.push(this.remove(node.firstChild))
			child = node.firstChild
		}
		return result
	},
	// ËÆæÁΩÆÂ±ûÊÄßÂíåËé∑ÂèñÂ±ûÊÄß
	attr(node, name, value) {
		if (arguments.length === 3) {
			node.setAttribute(name, value)
		} else if (arguments.length === 2) {
			return node.getAttribute(name)
		}
	},
	// ËÆæÁΩÆËäÇÁÇπÊñáÊú¨ÂÜÖÂÆπÔºå1 ‰∏™ÂèÇÊï∞ÊòØËé∑ÂèñÔºå2 ‰∏™ÂèÇÊï∞ÊòØËÆæÁΩÆ
	text(node, value) {
		if (arguments.length === 2) {
			if ('innerText' in node) {
				node.innerText = value
			} else {
				node.contenText = value
			}
		} else if (arguments.length == 1) {
			return 'innerText' in node ? node.innerText : node.contenText
		}
	},
	// ËÆæÁΩÆÊ†∑ÂºèÔºö
	// 1 ‰∏™ÂèÇÊï∞ÊòØËøîÂõûËäÇÁÇπÁöÑstyleÔºå
	// 2 ‰∏™ÂèÇÊï∞Êó∂ÔºåÊòØÂ≠óÁ¨¶‰∏≤Â∞±ËøîÂõûÊ†∑ÂºèÔºåÊòØÂØπË±°Âàô‰ª•ÈîÆÂÄºÂØπÁöÑÊñπÂºèËÆæÁΩÆÊ†∑Âºè„ÄÇ
	// 3 ‰∏™ÂèÇÊï∞ÊòØËÆæÁΩÆÊüê‰∏™ÁâπÂÆöÁöÑÊ†∑ÂºèÔºåÊØîÂ¶ÇËÆæÁΩÆcolor
	style(node, name, value) {
		if (arguments.length === 3) {
			node.style[name] = value
		} else if (arguments.length === 2) {
			if (typeof name === 'string') {
				return node.style[name]
			} else if (name instanceof Object) {
				for (key in name) {
					node.style[key] = name[key]
				}
			}
		}
	},
	// ËÆæÁΩÆ„ÄÅÂèñÊ∂à„ÄÅÊü•ËØ¢Á±ªÊ†∑Âºè
	class: {
		add(node, className) {
			node.classList.add(className)
		},
		remove(node, className) {
			node.classList.remove(className)
		},
		has(node, className) {
			return node.classList.contains(className)
		},
	},
	// ËÆæÁΩÆ„ÄÅÂèñÊ∂àÁõëÂê¨ÂáΩÊï∞
	on(node, eventName, fn) {
		node.addEventListener(eventName, fn)
	},
	off(node, eventName, fn) {
		node.removeEventListener(eventName, fn)
	},
	// Êü•ÊâæÂÖÉÁ¥†ÔºåÊúâÊü•ÊâæËåÉÂõ¥Â∞±Âú®ËåÉÂõ¥ÂÜÖÊâæÔºåÊ≤°ÊúâÂ∞±‰ªédocumentÂÜÖÊâæ„ÄÇ
	find(selector, scope) {
		return (scope || document).querySelectorAll(selector)
	},
	parent(node) {
		return node.parentNode
	},
	children(node) {
		return node.children
	},
	// ÊâæÂà∞ÊâÄÊúâÂÖÑÂºüËäÇÁÇπ
	siblings(node) {
		return Array.from(node.parentNode.children).filter((n) => n !== node)
	},
	next(node) {
		let x = node.nextSibling
		while (x && x.nodeType === 3) {
			x = x.nextSibling
		}
		return x
	},
	previous(node) {
		let x = node.previousSibling
		while (x && x.nodeType === 3) {
			x = x.previousSibling
		}
		return x
	},
	// ÈÅçÂéÜËäÇÁÇπ‰∏ãÁöÑÊâÄÊúâÂÖÉÁ¥†
	each(nodeList, fn) {
		for (let i = 0; i < nodeList.length; i++) {
			fn.call(null, nodeList[i])
		}
	},
	// Ëé∑ÂèñÂΩìÂâçËäÇÁÇπÁöÑ‰∏ãÊ†á
	index(node) {
		const list = this.children(node.parentNode)
		let i
		for (i = 0; i < list.length; i++) {
			if (list[i] === node) {
				break
			}
		}
		return i
	},
}
